@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono";
  font-display: swap;
  src: url("../fonts/pt-mono-regular.woff2") format("woff2"), url("../fonts/pt-mono-regular.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/roboto-condensed-bold.woff2") format("woff2"), url("../fonts/roboto-condensed-bold.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/roboto-condensed-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-regular.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/roboto-medium.woff2") format("woff2"), url("../fonts/roboto-medium.woff") format("woff"); }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0); }

.scroll-lock {
  position: fixed;
  overflow: hidden; }

.scroll-unlock {
  position: relative;
  overflow: auto; }

.container {
  min-width: 1024px;
  max-width: 1230px;
  margin: 0 auto;
  padding: 0 35px; }
  @media (max-width: 1023px) {
    .container {
      min-width: 768px;
      padding: 0 25px; } }
  @media (max-width: 767px) {
    .container {
      min-width: 320px;
      padding: 0 15px; } }

.page-body {
  display: grid;
  min-height: 100%;
  font-style: normal;
  grid-template-rows: min-content 1fr min-content; }

*,
*::before,
*::after {
  box-sizing: border-box; }

html {
  box-sizing: border-box; }

html,
body {
  width: auto;
  height: 100%;
  margin: 0 auto;
  padding: 0; }

a {
  color: inherit;
  text-decoration: none; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

.header {
  display: grid;
  max-width: 1631px;
  margin: 0 auto;
  padding: 0 110px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #ffffff;
  grid-template-columns: min-content 1fr min-content; }

.header__logo-wrapper {
  display: flex;
  width: 204px;
  margin-right: 100px; }

.header__logo {
  margin-right: 14px; }

.header__slogan {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-top: 3px;
  padding-left: 2px; }

.header__slogan-title {
  margin: 0;
  margin-bottom: 2px;
  font-weight: 700;
  font-size: 25px;
  line-height: 29px; }

.header__slogan-description {
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  font-family: "Roboto", "Arial", sans-serif;
  color: #788891; }

.header__feedback {
  display: flex;
  justify-content: center;
  width: 180px;
  margin-top: 3px;
  margin-bottom: 2px;
  padding: 14px;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  border: 1px solid #ffffff;
  border-radius: 5px; }
  .header__feedback:hover {
    color: #00b2ff;
    border: 1px solid #00b2ff; }
  .header__feedback:active {
    opacity: 0.7; }
  @media (max-width: 1023px) {
    .header__feedback {
      display: none; } }

.contacts {
  display: flex;
  justify-content: flex-end;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  font-family: "PT Mono", "Courier", monospace;
  justify-self: flex-end; }

.contacts__list {
  display: grid;
  width: 100%;
  margin: 0;
  margin-right: 38px;
  padding: 0;
  list-style: none;
  grid-column-gap: 65px;
  grid-row-gap: 30px;
  grid-template-columns: repeat(2, max-content); }
  @media (max-width: 1023px) {
    .contacts__list {
      margin-top: -3px;
      margin-right: 0;
      grid-column-gap: 9px; } }

.contacts__item {
  display: flex;
  justify-content: flex-end;
  align-self: center;
  max-width: 231px; }

.contacts__link {
  display: flex;
  align-items: center;
  padding: 12px; }
  .contacts__link:hover {
    color: #00b2ff; }
  .contacts__link:active {
    opacity: 0.7; }

.contacts__mail svg {
  margin-right: 20px; }
  @media (max-width: 1023px) {
    .contacts__mail svg {
      width: 26px;
      height: 19px;
      margin-right: 0; } }

@media (max-width: 1023px) {
  .contacts__phone {
    padding-right: 0; } }

.contacts__phone svg {
  margin-right: 16px; }
  @media (max-width: 1023px) {
    .contacts__phone svg {
      width: 24px;
      height: 24px;
      margin-right: 0; } }

.contacts__span {
  display: flex; }
  @media (max-width: 1023px) {
    .contacts__span {
      display: none; } }

.intro {
  width: 100%;
  padding-top: 33px;
  background-color: #1b2d37;
  background-image: url("../../img/content/intro-background-desktop-@1x.png"), url("../../img/svg/intro-block.svg");
  background-size: cover; }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .intro {
      background-image: url("../img/content/intro-background-desktop-@2x.png"), url("../../img/svg/intro-block.svg"); } }
  @media (max-width: 1023px) {
    .intro {
      padding-top: 20px; } }

.main-screen {
  display: flex;
  max-width: 1631px;
  margin: 0 auto;
  padding: 173px 0 108px 105px;
  color: #ffffff; }
  @media (max-width: 1023px) {
    .main-screen {
      position: relative; } }

.main-screen__info-block {
  margin-right: 71px;
  padding-top: 58px; }

.main-screen__title {
  max-width: 718px;
  margin: 0;
  margin-bottom: 27px;
  font-weight: 700;
  font-size: 80px;
  line-height: 94px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
