  margin-left: 98px;
  padding: 18px;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  background-color: #00b2ff;
  border-radius: 5px; }
  .main-screen__link:hover {
